@media screen and (min-width: 1000px) {
  .joke-headline {
    font-size: 3rem;
  }

  .joke-button-container {
    height: 5vh;
  }

  .generateJoke {
    font-size: 2.5rem;
    padding: 0rem 5%;
  }

  .addJoke {
    font-size: 1.5rem;
    padding: 0rem 1%;
  }

  .joke-show-element {
    font-size: 2.5rem;
    line-height: 2.5rem;
    margin: 5% 0;
  }

  .joke-list-section__joke-headline {
    font-size: 2.5rem;
  }

  .joke-div {
    padding: 0 0 0 15%;
  }

  .joke {
    font-size: 2.2rem;
  }

  .deleteJoke {
    font-size: 2rem;
    padding: 0 1%;
  }
}

@media screen and (max-width: 1000px) {
  .joke-headline {
    font-size: 3rem;
  }

  .joke-button-container {
    height: 5vh;
  }

  .generateJoke {
    font-size: 2rem;
    padding: 0rem 5%;
  }

  .addJoke {
    font-size: 1.5rem;
    padding: 0rem 2%;
  }

  .joke-show-element {
    font-size: 2rem;
    line-height: 2.5rem;
    margin: 10% 10%;
  }

  .joke-list-section__joke-headline {
    font-size: 2rem;
  }

  .joke {
    font-size: 1.7rem;
  }

  .deleteJoke {
    font-size: 1.7rem;
    padding: 0 1.5%;
  }
}

@media screen and (max-width: 600px) {
  .joke-headline {
    font-size: 2.5rem;
  }

  .joke-button-container {
    height: 5vh;
  }

  .generateJoke {
    font-size: 1.7rem;
    padding: 0rem 5%;
  }

  .addJoke {
    font-size: 1.1rem;
    padding: 0rem 2%;
  }

  .joke-show-element {
    font-size: 1.7rem;
    line-height: 2rem;
    margin: 15% 10%;
  }

  .joke-list-section__joke-headline {
    font-size: 1.7rem;
  }

  .joke {
    font-size: 1.5rem;
  }

  .deleteJoke {
    font-size: 1.5rem;
    padding: 0 1.5%;
  }
}

@media screen and (max-width: 300px) {
  .joke-headline {
    font-size: 1.5rem;
  }

  .joke-button-container {
    height: 3vh;
  }

  .generateJoke {
    font-size: 1rem;
    padding: 0rem 2%;
    margin-left: 20%;
  }

  .addJoke {
    font-size: 1.1rem;
    margin-left: 5%;
  }

  .joke-show-element {
    font-size: 1rem;
    line-height: 1.5rem;
    margin: 20% 10%;
  }

  .joke-list-section__joke-headline {
    font-size: 1.3rem;
  }

  .joke {
    font-size: 1.1rem;
  }

  .deleteJoke {
    font-size: 1.1rem;
  }
}
