@import url("https://fonts.googleapis.com/css2?family=Cookie&family=Dancing+Script:wght@400..700&family=Pacifico&display=swap");

body {
  text-align: center;
  margin: 0;
  padding: 0;
  --font-art: "Cookie", cursive;
  font-family: var(--font-art);
}

.button {
  font-family: var(--font-art);
}

.button:hover {
  cursor: pointer;
}

.generateJoke,
.addJoke {
  margin: 0;
  height: 100%;
  padding: 0 1%;
}

.generateJoke {
  margin-left: 5%;
  font-size: 1.5rem;
}

.addJoke {
  margin-left: 4%;
  font-size: 1.2rem;
}

.joke-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
}

.joke-show-element {
  margin-top: 5vh;
}

.joke-list-section {
  margin: 5vh 0 0 0;
}

.joke-list-section__joke-headline {
  font-size: 1.5rem;
}

.joke-div {
  display: flex;
  padding: 0 0 0 25%;
}

.joke {
  margin: 0;
  width: 50%;
  text-align: left;
  max-width: 60%;
  padding: 5% 5% 5% 0;
}

.deleteJoke {
  padding: 0 0.5%;
  font-size: 1.6rem;
  margin: auto 0 auto 10%;
  max-height: 5vh;
}
